/* Í≥µÌÜµ Ïä§ÌÉÄÏùº */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.5rem;
  height: 3.75rem;
  background-color: white;
  border-bottom: 1px solid #ddd;
  font-family: 'Pretendard', sans-serif;
  font-size: clamp(0.875rem, 1.2vw, 1rem);
  flex-wrap: wrap;
  position: relative;
}

/* ÌÉÄÏù¥ÌãÄ + ÌñÑÎ≤ÑÍ±∞Î•º Í∞êÏã∏Îäî Ïª®ÌÖåÏù¥ÎÑà */
.navbar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
}

/* ÌÉÄÏù¥ÌãÄ */
.navbar-header .title {
  font-weight: 700;
  font-size: clamp(1rem, 2.5vw, 1.25rem);
  text-decoration: none;
  color: #1b1b1b;
}

/* Í∞ÄÏö¥Îç∞ Î©îÎâ¥ */
.navbar-center {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  flex: 2;
  text-align: center;
}

.nav-link {
  text-decoration: none;
  color: #333;
  font-weight: 500;
  font-size: clamp(0.875rem, 1.2vw, 1rem);
  transition: color 0.2s;
}

.nav-link:hover {
  color: #0d9488;
}

/* Í≥µÌÜµ Î°úÍ∑∏Ïù∏ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.login-btn {
  font-size: clamp(0.875rem, 1.2vw, 1rem);
  background-color: #22c55e;
  color: white;
  font-weight: 600;
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  text-decoration: none;
  transition: background-color 0.2s;
  white-space: nowrap;
}

/* Îç∞Ïä§ÌÅ¨ÌÉëÏö© Î°úÍ∑∏Ïù∏ Î≤ÑÌäº */
.desktop-login {
  display: flex;                /* ‚úÖ flex ÏÑ§Ï†ï */
  align-items: center;          /* ‚úÖ ÏÑ∏Î°ú Ï§ëÏïô Ï†ïÎ†¨ */
  justify-content: center;      /* Í∞ÄÎ°ú Ï†ïÎ†¨ÎèÑ ÌïÑÏöî Ïãú */
  height: 100%;                 /* ‚úÖ navbar ÎÜíÏù¥ÎßåÌÅº Ï∞®ÏßÄ */
  background-color: #22c55e;
  color: white;
  font-weight: 600;
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  text-decoration: none;
  transition: background-color 0.2s;
  white-space: nowrap;
  font-size: clamp(0.875rem, 1.2vw, 1rem);
}

.desktop-login:hover {
  background-color: #16a34a;
}

/* Î™®Î∞îÏùºÏö© Î°úÍ∑∏Ïù∏ Î≤ÑÌäº (Í∏∞Î≥∏ Ïà®ÍπÄ) */
.mobile-login {
  display: none;
}

/* ÌñÑÎ≤ÑÍ±∞ Î©îÎâ¥ Î≤ÑÌäº (Í∏∞Î≥∏ Ïà®ÍπÄ) */
.hamburger {
  display: none;
  font-size: 1.75rem;
  background: none;
  border: none;
  cursor: pointer;
  align-self: center;
}

/* ‚úÖ ÎØ∏ÎîîÏñ¥ ÏøºÎ¶¨: Î™®Î∞îÏùº ÎåÄÏùë (1200px Ïù¥Ìïò) */
@media screen and (max-width: 1200px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    height: auto;
    padding: 1rem;
  }

  .navbar-header {
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  .hamburger {
    display: block;
    align-self: center;
    margin: 0;
  }

  .navbar-center {
    display: none;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 0.75rem;
    margin-bottom: 1rem;
    border-top: 1px solid #eee;
    padding: 1rem 0;
    background-color: white;
  }

  .navbar-center.active {
    display: flex;
  }

  .nav-link {
    width: 100%;
    text-align: center;
    padding: 0.5rem 0;
  }

  .desktop-login {
    display: none;
  }

  .mobile-login {
    display: block;
    background: linear-gradient(to right, #22c55e, #16a34a);
    color: white;
    font-weight: 600;
    padding: 0.75rem 1rem;
    border-radius: 0.5rem;
    font-size: 1rem;
    margin-top: 1rem;
    transition: background 0.3s;
    text-align: center;
    width: 90%;
  }

  .mobile-login:hover {
    background: linear-gradient(to right, #16a34a, #15803d);
  }

  .desktop-logout {
    display: none;
  }

  .mobile-logout {
    display: block;
  }
}

/* ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ïä§ÌÉÄÏùº */
.user-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.user-name {
  color: #333;
  font-weight: 500;
  font-size: 0.9rem;
}

.logout-btn {
  background-color: #dc2626;
  color: white;
  font-weight: 600;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.375rem;
  cursor: pointer;
  transition: background-color 0.2s;
  text-decoration: none;
  font-size: clamp(0.875rem, 1.2vw, 1rem);
}

.logout-btn:hover {
  background-color: #b91c1c;
}

.desktop-logout {
  display: block;
}

.mobile-logout {
  display: none;
  background: linear-gradient(to right, #dc2626, #b91c1c);
  color: white;
  font-weight: 600;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 1rem;
  margin-top: 1rem;
  transition: background 0.3s;
  text-align: center;
  width: 90%;
}

.mobile-logout:hover {
  background: linear-gradient(to right, #b91c1c, #991b1b);
}
